// Footer
.footer
  background: $accent-bg
  padding: 43px 0 27px
  &-top
    display: flex
    flex-direction: row
    justify-content: space-between
    margin-bottom: 46px
    &__sale
      margin-right: 50px
  &-bottom
    display: flex
    flex-direction: row
    justify-content: center
    &__link
      font-size: 10px
      line-height: 12px
      color: #48494D
      &:first-child
        margin-right: 80px

.sale-footer
  &__title
    width: 225px
    margin-bottom: 35px
  &__form
    width: 295px
    height: 33px

.form-footer
  position: relative
  &__input
    width: 100%
    font-size: 14px
    color: #C4C4C4
    border: none
    padding: 8px 0 8px 12px
    background: $white-color
  &__btn
    background: $accent-color
    font-weight: 600
    font-size: 11px
    line-height: 1.19
    letter-spacing: 0.12em
    text-transform: uppercase
    color: $white-color
    padding: 10px 16px
    border: none
    cursor: pointer
    position: absolute
    right: 0px
    &:hover
      background-color: #6292dc
      border-radius: 8px
      box-shadow: 3px 5px 20px rgb(50 50 50 / 35%)

.info-footer
  width: 200px
  &__title
    margin-bottom: 12px
  &__list
    font-size: 14px
  &__item
    margin-bottom: 10px

.about-footer
  width: 200px
  &__title
    margin-bottom: 12px
  &__list
    font-size: 14px
  &__item
    margin-bottom: 10px

.social-footer
  width: 250px
  &__list
    height: 100%
    display: flex
    flex-direction: row
    justify-content: space-between
    align-items: center
  &__item + .social-footer__item
    margin-left: 30px
    &:last-child
      margin-right: 70px
// ./footer