// 1 block
.main-banner
  padding-top: 80px
  &__inner
    display: flex
    justify-content: space-between
  &__sale
    border: 1px solid #CDCDCD
    border-radius: 3px

.slider-banner
  max-width: 870px
  &__btn
    position: absolute
    top: 50%
    transform: translate(-50%)
    background-color: transparent
    border: none
    z-index: 5
    cursor: pointer
    &_prev
      left: 40px
    &_next
      right: 20px
  &__link img
    object-fit: cover
    min-height: 400px

.slick-dots
  position: absolute
  bottom: 40px
  left: 0
  right: 0
  text-align: center
  li
    display: inline-block
    padding: 0 5px
  button
    width: 11px
    height: 11px
    font-size: 0
    border: 1px solid #FFFFFF
    border-radius: 50%
    background-color: transparent
    cursor: pointer

li.slick-active button
  background-color: $white-color

.sale-banner
  display: flex
  flex-direction: column
  width: 265px
  text-align: center
  font-size: 18px
  line-height: 1.17
  &__top
    display: flex
    justify-content: space-between
    align-items: flex-start
  &__content
    flex: 1
  &__img
    margin-bottom: 5px
    object-fit: contain
  &__title
    font-weight: 700
    margin-bottom: 30px
    padding: 0 20px
    margin-bottom: 7px
  &__footer
    background-color: $accent-bg

.footer-sale
  padding: 26px 20px
  &__text
    margin-bottom: 3px
    span
      display: block
      font-weight: 700
      line-height: 1.22
      color: $accent-color

.top-sale
  &__info
    font-size: 12px
    line-height: 1.25
    letter-spacing: 0.1em
    text-transform: uppercase
    color: $white-color
    padding: 8px 20px
    background-color: $accent-color
  &__price
    font-family: "BarlowS", sans-serif
    text-align: right

.price-sale
  position: relative
  &__new
    font-weight: 700
    font-size: 27px
    color: $accent-color
    padding-right: 10px
    span
      img
        width: 18px
        height: 19px
  &__old
    display: inline-block
    font-size: 14px
    line-height: 1.22
    color: $gray-text
    margin-right: 3px
    padding: 2px 6px
    background: url('../img/icons/price-line.svg') no-repeat
    svg
      width: 12px
      height: 12px
      fill: $gray-text
