// 4block
.products
  &__tabs
    margin-bottom: 25px
  &__btn
    text-align: center
    padding-top: 35px
    &-link
      background: $accent-bg
      padding: 17px 43px
      font-size: 15px
      letter-spacing: 0.12em
      text-transform: uppercase
      &:hover
        background: #eaeaee
        border-radius: 8px
        box-shadow: 5px 5px 20px rgba(50, 50, 50, 0.65)

.tabs-products
  &__tab
    margin-right: 50px
    padding-bottom: 10px
    text-transform: lowercase
  &__tab:last-child
    margin-right: 0
  &__tab.tab-item_active
    font-weight: 500
    border-bottom: 2px solid $accent-color

.slider-products
  &__item
    position: relative
    text-align: center
    margin-right: 30px
  &__card
    &_sale::before
      content: "Sale"
      position: absolute
      left: 0
      top: 0
      text-transform: uppercase
      color: $white-color
      background-color: $accent-color
      padding: 8px 15px
      font-size: 12px
      line-height: 1.17
      letter-spacing: 0.07em
  &__btn
    position: absolute
    top: 50%
    transform: translate(-50%)
    background-color: transparent
    border: none
    z-index: 5
    cursor: pointer
    &_prev
      left: -30px
    &_next
      right: -30px

.item-products
  width: 271px
  height: 403px
  padding: 52px 20px 31px
  border: 1px solid #CDCDCD
  border-radius: 3px
  &__hover-text
    text-transform: lowercase
    padding: 18px 21px
    background-color: rgba(255, 255, 255, .9)
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2)
    border-radius: 3px
    display: inline-block
    position: absolute
    top: 30%
    opacity: 0
    transition: all .3s
  &__favorites
    position: absolute
    top: 19px
    right: 19px
    width: 25px
    height: 23px
    border: none
    background: transparent
    cursor: pointer
    &::before
      content: ""
      width: 24px
      height: 22px
      position: absolute
      top: 0
      left: 0
      background: url('../img/icons/heart.svg') center / contain no-repeat
    &_active::before
      background: url('../img/icons/heart-active.svg') no-repeat
  &__title
    font-weight: normal
    font-size: 18px
    padding: 14px 0 25px
  &__price
    font-weight: 700
    font-size: 30px
    line-height: 36px
    span
      img
        width: 24px
        height: 23px
        &:hover .item-products
          color: $accent-color
  &__cart
    position: absolute
    width: 60px
    height: 40px
    right: 0px
    bottom: 0px
    background: $accent-color
    border: none
    border-radius: 10px 0px 0px 0px
    cursor: pointer
    img
      padding-top: 7px
  &__notice-text
    display: none
    font-weight: 700
    font-size: 18px
    line-height: 1.17
    padding-top: 8px
  &__notice-link
    display: none
    font-size: 16px
    line-height: 1.19
    text-decoration: underline
    color: $accent-color
    position: absolute
    left: 27px
    bottom: 25px
  &__img
    width: 100%

.item-products:hover .item-products__hover-text
  opacity: 1
  color: $main-text

.item-products:hover
  box-shadow: 3px 3px 20px rgba(50, 50, 50, 0.25)
  border: none
  border-radius: 3px
  color: $accent-color

.item-products:hover .item-products__notice-text
  color: $main-text

.slider-products__item.slider-products__item_not-available .item-products__title
  padding-bottom: 7px

.slider-products__item.slider-products__item_not-available .item-products__notice-text,
.slider-products__item.slider-products__item_not-available .item-products__notice-link
  display: block

.slider-products__item.slider-products__item_not-available .item-products__price,
.slider-products__item.slider-products__item_not-available .item-products__cart
  display: none

.slick-slide img
  display: inline-block
// ./products
