.catalog
  margin-bottom: 100px
  &__title
    font-weight: bold
    font-size: 30px
    margin-bottom: 26px
  &__filter
    display: flex
    justify-content: space-between
    margin-bottom: 50px
  &__inner
    display: flex
  &__pagination
    width: 100%
    margin-top: 70px

.filter-catalog
  &__items button
    font-size: 14px
    opacity: 0.7
    background: $accent-bg
    border-radius: 3px
    border: none
    padding: 5px 22px
    cursor: pointer
  &__items button + button
    margin-left: 8px
  &__btn button
    background-color: transparent
    border: none
    cursor: pointer
  &__btn button + button
    margin-left: 10px

.inner-catalog
  &__aside
    min-width: 290px
    margin-right: 10px
  &__list
    display: flex
    justify-content: space-between
    flex-wrap: wrap
  &__list .item-products
    margin-bottom: 30px
    margin-right: 0

.pagination-catalog
  &__list
    display: flex
    justify-content: center
  &__item
    padding: 6px 12px
    border: 2px solid transparent
    margin: 0 6px
    font-family: "BarlowS", sans-serif
    font-weight: 400
    font-size: 18px
    color: #c4c4c4
    &_active
      border: 2px solid $accent-color
      font-weight: 600
      color: $accent-color
    a
      display: block

.aside-filter
  &__tabs
    display: flex
    justify-content: space-between

.tabs-filter-aside
  &__tab
    width: 50%
    font-weight: 400
    font-size: 16px
    line-height: 1.18
    letter-spacing: 0.09em
    text-transform: uppercase
    color: #C4C4C4
    &.tab-item_active
      font-weight: 700
      color: $black-color
      span
        display: inline-block
        padding-bottom: 7px
        border-bottom: 2px solid #1c62CD

.content-filter-aside
  &__item-drop
    margin: 40px 0
  &__item-text
    font-weight: 600
    font-size: 16px
    padding-left: 26px
  &__content
    display: flex
    &-box
      width: 50%
    &-label
      font-size: 14px
      cursor: pointer

.aside-filter__item-drop
  margin-bottom: 20px
  position: relative
  cursor: pointer
  &::before
    content: ""
    position: absolute
    left: 0
    bottom: 5px
    width: 12px
    height: 8px
    background: url('../img/catalog/icons/arrow-down.svg') center no-repeat
    transform: rotate(0deg)
  &_active::before
    transform: rotate(180deg)
