*
  box-sizing: border-box

a
  text-decoration: none
  color: inherit
  display: inline-block

$accent-bg: #F0F0F4
$accent-color: #1C62CD
$main-text: #2F3035
$gray-text: #C4C4C4
$white-color: #fff
$black-color: #000
$dark-color: #48494D

body
  font-family: "SFProDisplayS", sans-serif
  color: $main-text
  font-size: 20px
  line-height: 1.2
  font-weight: 400
  font-style: normal

.container
  max-width: 1200px
  padding: 0 15px
  margin: 0 auto

.page-section
  margin-bottom: 80px

.title-h2
  font-family: "BarlowS", sans-serif
  font-weight: 700
  font-size: 25px
  line-height: 1.2
  color: inherit
  margin-bottom: 25px

.title-h6
  font-weight: 700
  font-size: 14px
