.breadcrumbs
  padding-top: 40px
  margin-bottom: 40px

.list-breadcrumbs
  &__item
    display: inline-block
    padding-right: 16px
    margin-right: 10px
    position: relative
    font-size: 15px
    line-height: 18px
    color: #C4C4C4
    &::after
      content: ""
      position: absolute
      right: 0
      bottom: 4px
      width: 7px
      height: 10px
      background: url('../img/icons/back-arrow.svg') 0 0 / cover no-repeat
      border: none

.list-breadcrumbs__item:last-child::after
  display: none