// Header
.header__top
  display: flex
  justify-content: space-between
  align-items: center
  min-height: 122px
  font-weight: 700
  &-address
    display: flex
    align-items: center

.menu
  &__item
    display: inline-block
    & + .menu__item
      padding-left: 50px

.address-header
  max-width: 315px
  padding-left: 5px
  &__link
    display: inline-block
  &__img
    width: 30px
    height: 30px
    display: inline-block
  &__text
    display: inline-block

.user-header
  &__list
    display: flex
    align-items: center
    margin-right: 15px
  &__item
    padding-right: 10px
    cursor: pointer

.cart
  position: relative
  &__count
    position: absolute
    font-weight: 500
    font-size: 10px
    line-height: 2
    color: $white-color
    width: 20px
    height: 20px
    background-color: $accent-color
    border-radius: 50%
    text-align: center
    bottom: -5px
    right: -14px

.header__bottom
  &-list
    display: flex
    justify-content: space-around
    align-items: center

.categories-header
  min-height: 53px
  background-color: $accent-bg
  &__item
    position: relative
  &__link
    padding: 15px 0 14px
    transition: text-shadow .3s
    &:hover
      text-shadow: 0 0 .9px #000, 0 0 .7px #333, 0 0 .7px #333
    &::before
      content: ""
      width: 0px
      height: 3px
      position: absolute
      bottom: 0px
      left: auto
      right: 0
      background-color: $accent-color
      transition: all .3s
    &:hover::before
      width: 100%
      right: auto
      left: 0
    // &_active
    //   text-shadow: 0 0 .9px #000, 0 0 .7px #333, 0 0 .7px #333
      &::before
        content: ""
        width: 100%
        height: 3px
        bottom: 0px
        left: 0
        background-color: $accent-color
// ./header
