// 3 block
.main-categories__list
  display: grid
  grid-template-columns: repeat(3, 1fr)
  gap: 30px

.list-categories
  &__item
    border: 1px solid #CDCDCD
    border-radius: 3px
    transition: all .3s
    &:hover
      box-shadow: 3px 3px 20px rgba(50, 50, 50, 0.25)
      border: none
    // &_active
    //   box-shadow: 3px 3px 20px rgba(50, 50, 50, 0.25)
    //   border: none
  &__link
    display: flex
    justify-content: space-between
    padding: 12px 20px
  &__description
    width: 50%
    min-height: 70px
    padding-top: 10px
    display: flex
    flex-direction: column
    justify-content: space-between
  &__title
    font-family: "BarlowS", sans-serif
    font-weight: 700
    font-size: 23px
    transition: all .3s
  &__text
    font-size: 14px
    color: $gray-text
    &::after
      content: url('../img/icons/back-arrow.svg')
      padding-left: 3px
  &__img
    width: 50%
    text-align: center
    img
      max-width: 100%
      height: 100px
      object-fit: contain

.list-categories__item:hover .list-categories__title
  color: $accent-color

