// 2 block
.main-search
  &__tabs
    display: flex
    align-items: center

.tabs-search
  &__item
    padding: 12px 26px
    margin-bottom: 37px

.content-search
  &__form
    display: flex
  &__input
    width: 100%
    background-color: $accent-bg
    border-radius: 3px
    padding: 5px 20px
    border: none
    font-size: 16px
    line-height: 1.19
    &::placeholder
      color: #656464
  &__btn
    font-weight: 700
    font-size: 14px
    letter-spacing: 0.12em
    text-transform: uppercase
    color: $white-color
    background-color: $accent-color
    padding: 17px 41px
    margin-left: -3px
    border-radius: 3px
    border: none
    cursor: pointer
    &:hover
      background-color: #6292dc
      border-radius: 8px
      box-shadow: 3px 5px 20px rgba(50, 50, 50, 0.35)

.tabs-search__item.tab-item_active
  border-radius: 5px
  background-color: $accent-bg
  font-weight: 700

.tab-content
  display: none

.tab-content.tab-content_active
  display: block
// ./tabs
